html, body {
    height: 100%;
}

.container {
    width: 100%;
    height: 100%;

    padding: 50px;

    box-sizing: border-box;
}

.box_trap {
 width: 100%;
 height: 300px;
 
 position: relative;
}

.file-loader {
    width: 0;
    height: 0;
}

.trap {
    position: absolute;
    top: 0; left: 0; bottom: 0; right: 0; margin: 0;

    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
    border: 1px solid black;
    border-radius: 10px;

    background:white;
}

.trap_title {
    font-size: 30px;
}

.box_preview {
    margin-top: 30px;
    display: flex;
}

.preview {
    width: 150px;
    max-width: 150px;

    height: 150px;
    max-height: 150px;

    position: relative;

    border: 1px solid black;

    margin-right: 20px;
}

.preview_img {
    width: 100%;
    height: 100%;
}

.btn-del {
    position: absolute;
    top: -15px; 
    right: -15px;

    content: '\274C';

    box-sizing: border-box;

    width: 30px;
    height: 30px;
    border-radius: 50%;

    padding: 0;
    margin: 0;

    background-color: white;
    font-size: 19px;
}
